export default {
	data() {
		return {
			info: {
				nickName: null,
				islogin: 0,
				isgetuserinfo: !1,
				loginnote: 0,
				getcodetimes: 0,
				gettokentimes: 0
			}
		};
	},
	methods: {

		GetUserInfo({ detail }) {
			console.log(detail)
			this.info = { ...JSON.parse(detail.rawData) }
			this.$store.commit("user/SET_USER_INFO", this.info)
		},
		/* 运动步数统计 */
		toSportGoal() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},
		/* 密码设置 */
		toChangePwd() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},
		/* 常见问题 */
		Tousetips() {
			uni.navigateTo({
				url: './useTip/index'
			});

		},
		/* 权限设置 */
		openset() {
			uni.openSetting({
				success(res) {
					console.log(res.authSetting)
				}
			});
		},
		/* 需要智能设备 */
		apploadnote() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		}

	},
	onShow() {
		try {
			this.info = this.$store.getters.userInfo
		} catch (e) {
			console.log(e)
			//TODO handle the exception
		}
	}
}
